import * as React from "react"
import { Link } from "gatsby"


const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
      <div>
      <h1 className="main-heading">
        <Link to="/">tak. 7.</Link>
      <ul className="ul-header">
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/">HOME</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/contact">CONTACT</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/about">ABOUT</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/works">WORKS</Link>
      </li>
      </ul>
      </h1>
      </div>
    )
  } else {
    header = (
      <div>
      <h1 className="main-headingatblog">
        <Link to="/">tak. 7.</Link>
      <ul className="ul-header">
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/">HOME</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/contact">CONTACT</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/about">ABOUT</Link>
      </li>
      <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/works">WORKS</Link>
      </li>
      {/* <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/interests">INTERESTS</Link>
      </li> */}
      {/* <li className="heading-menu-li">
        <Link className="heading-menu-a" to="/">HOME</Link>
      </li> */}
      </ul>
      </h1>
      </div>
    )
  }

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer className="copyright-footer">
        Copyright © 2025 Nanahara Takanori.
      </footer>
    </div>
  )
}

export default Layout
