import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { StaticImage } from "gatsby-plugin-image"

const NotFoundPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <h1 className="h1-aboutpage">Interests</h1>
      <p>
        <h4>研究の興味<br></br>Interests as research</h4>
        広く言えば空間認知全体に興味があります．<br></br>
        都市・建築空間における利用者の経路探索やナビゲーション行動に関する戦略，空間の表象とその構造について研究しています．<br></br>
        I am interested in the whole field of spatial cognition. My current topics are strategies which people use during navigation and wayfinding, and the structure of spatial representation. <br></br>
        <h4>日常の興味<br></br>Interests as a hobby</h4>
        紅茶とワインを常に飲んでいます．<br></br>
        I love tea and wine.
      </p>
    </Layout>
  )
}

export const Head = () => <Seo title="Interests" />

export default NotFoundPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`